import { Component, OnInit } from '@angular/core';
import io from 'socket.io-client';
@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {
  txtMes = "";
  mes : string[] = [
    'hello',
    'how are you'
  ];
  socket = io('https://chat1003.herokuapp.com/');
  constructor() { }

  ngOnInit() {
    this.socket.on('SERVER_SEND_MES',mes =>{
       this.mes.push(mes);
    });
  }
  sendMes(){
    this.socket.emit('CLIENT_SEND_MES',this.txtMes);
    this.mes.push(this.txtMes);
    this.txtMes ='';
  }

}



